import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IOptionsPsOptionFacilityExposed } from './ps-option-facility.component.interface';


@Component({
  selector: 'ps-option-facility',
  templateUrl: './ps-option-facility.component.html',
  styleUrls: ['./ps-option-facility.component.scss'],
})
export class PsOptionFacilityComponent extends PsTemplateListPage implements OnInit {
  @Input() options: IOptionsPsOptionFacilityExposed;
  @Output() public onClickCard = new EventEmitter<any>();


  containerLookUpOptions: IOptionsPsContainerLookupOptionComponentExposed = {};
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  private showSingleAccount = false;
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  userInformation: any;
  tradeFinSec: any;


  constructor(commonProv: PsCommonService,
    private cdRef?: ChangeDetectorRef) {
    super();
  }

  ngOnInit() {
    super.init();
    this.populateHeaderValueMap();
    this.userInformation = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    this.tradeFinSec = this.userInformation?.businessProfiles.find((i) => i.operVO.OPER_ID === CommonBussinessConstant.TRADE_FIN_OPER_ID);
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
  }

  getContainerLookUpOptions(item) {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.currencyDescription = 'currencyBriefName';
    lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.balanceMapping = PsCommonSettings.oper_ID !== CommonBussinessConstant.MY_FACILITIES_OPER_ID ? this.balanceMapping() : null;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.editRequestMap = this.editRequestMap;
    //lookupDetails.showInitialCardValues = 6; // added to show all card values
    lookupDetails.itemList = this.options.itemList;
    if (PsCommonSettings.oper_ID === CommonBussinessConstant.MY_FACILITIES_OPER_ID && item.facilityStatus === 'A') {
      lookupDetails.actionDetailsOptions = [
        {
          id:'facility_statement_key',
          label: 'facility_statement_key',
          actionHyperlink: {
            iconOptions: {
              iconName: 'document',
              labelOptions: {
                labelKey: 'facility_statement_key'
              }
            },
            route: 'facility-statement',
            pageOptions: {
              operId: CommonBussinessConstant.FACILITY_STATEMENT_OPER_ID,
              iconName: 'edit1',
              title: 'facility_statement_key'
            },
            navigationOptions: {
              queryParams: {},
            },
          }
        },
        {
          id:'settlement_schedule_key',
          label: 'settlement_schedule_key',
          actionHyperlink: {
            iconOptions: {
              iconName: 'document',
              labelOptions: {
                labelKey: 'settlement_schedule_key'
              }
            },
            route: 'repayment-schedule-report',
            pageOptions: {
              operId: CommonBussinessConstant.SETTLEMENT_SCHEDULE_OPER_ID,
              iconName: 'document',
              title: 'settlement_schedule_key'
            },
            navigationOptions: {
              queryParams: {},
            },
          }
        },
        ((this.tradeFinSec?.children.some((i) => i.operVO.OPER_ID === CommonBussinessConstant.LC_REQ_OPER_ID) && item.isEligibleLC) ? {
          label: 'request_new_lc_key',
          actionHyperlink: {
            iconOptions: {
              iconName: 'edit1',
              labelOptions: {
                labelKey: 'request_new_lc_key'
              }
            },
            route: 'letter-of-credit-request',
            pageOptions: {
              operId: CommonBussinessConstant.LC_REQ_OPER_ID,
              iconName: 'document',
              title: 'request_new_lc_key'
            },
            navigationOptions: {
              queryParams: {
                disableCustomComponents: true,
                settlementMethod: 'I',
                facilityDetails: item.facilityNumber,
                facilityDetails_lookupKey: item.facilityNumber
              },
            },
          }
        } : {}),
        ((this.tradeFinSec?.children.some((i) => i.operVO.OPER_ID === CommonBussinessConstant.LG_REQ_OPER_ID) && item.isEligibleLG) ? {
          label: 'request_new_lg_key',
          actionHyperlink: {
            iconOptions: {
              iconName: 'edit1',
              labelOptions: {
                labelKey: 'request_new_lg_key'
              }
            },
            route: 'letter-of-guarantee',
            pageOptions: {
              operId: CommonBussinessConstant.LG_REQ_OPER_ID,
              iconName: 'document',
              title: 'request_new_lg_key'
            },
            navigationOptions: {
              queryParams: {
                disableCustomComponents: true,
                facilityDetails: item.facilityNumber,
                facilityDetails_lookupKey: item.facilityNumber
              },
            },
          }
        } : {})
      ];
    }

    this.containerLookUpOptions.statementOptions = {
      buttonIcon: 'list-box',
      redirectUrl: 'facility-statement-report'
    };
    return lookupDetails;
  }

  private populateHeaderValueMap() {
    let amountMap: IMapKeyValue = {};
    let expiryDateMap: IMapKeyValue = {};
    let utilizedAmountMap: IMapKeyValue = {};
    let unutilizedAmountMap: IMapKeyValue = {};
    let branchNameMap: IMapKeyValue = {};
    let outstandingMap: IMapKeyValue = {};
    let facilityTypeMap: IMapKeyValue = {};
    let facilityTypeDescMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let statusDescMap: IMapKeyValue = {};

    amountMap = {
      key: 'facilityAmount',
      value: '0.0'
    };

    facilityTypeMap = {
      key: 'facilityType',
      value: '',
      isDetailOption: false
    };

    facilityTypeDescMap = {
      key: 'facilityTypeDesc',
      value: '',
      isDetailOption: false
    };

    utilizedAmountMap = {
      key: 'utilizedAmount',
      value: ''
    };

    unutilizedAmountMap = {
      key: 'unutilizedAmount',
      value: '0.0'
    };

    outstandingMap = {
      key: 'outstanding',
      value: '',
      isDetailOption: false
    };

    expiryDateMap = {
      key: 'expiryDate',
      value: '--/--/----',
      isDetailOption: false
    }

    branchNameMap = {
      key: 'branchName',
      value: '',
      showMappingOnNoValue: true
    };

    statusDescMap = {
      key: 'statusDesc',
      value: '',
      showMappingOnNoValue: true
    };


    headerTitleMap = {
      key: 'title',
      value: 'facilityNumber',
      isEdit: false,
      formGroupMap: {
        placeHolder: 'facilityNumber',
        fcName: 'facilityNoTitle',
      }
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'revolvingOneOffDesc',
      isEdit: false,
    };


    this.labelValuesMap.set('facility_amount_key', amountMap);
    this.labelValuesMap.set('utilized_key', utilizedAmountMap);
    this.labelValuesMap.set('unutilized_key', unutilizedAmountMap);
    if (this.options.isFacilityActive) {
      this.labelValuesMap.set('outstanding_key', outstandingMap);
    }
    this.labelValuesMap.set('type_key', facilityTypeDescMap);
    this.labelValuesMap.set('expiry_date_key', expiryDateMap);
    this.labelValuesMap.set('branch_key', branchNameMap);
    this.labelValuesMap.set('status_key', statusDescMap);

    // will be used as header to template card in look up details component
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);

  }

  updateCard(facility: any) {
    if (facility && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === facility);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }

  private balanceMapping(): any[] {
    const balanceMappingList = [
      {
        key: 'facility_amount_key',
        value: 'facilityAmount'
      },
      {
        key: 'unutilized_amount_key',
        value: 'unutilizedAmount'
      }
    ];

    return balanceMappingList;
  }


  ngAfterViewChecked() {
    this.cdRef.markForCheck();
    // if (this.options && (this.options.itemList === null || this.options.itemList === undefined)) {
    //   this.options.itemList = this.options.listOfOptions;
    //   this.showSingleAccount = false;
    // } else if (this.options && this.options.itemList && this.options.itemList.length === 1 && this.showSingleAccount === false) {
    //   this.options.itemList = this.options.listOfOptions;
    // } else if (this.options && this.options.showItemPopUp === undefined) {
    //   this.options.itemList = this.options.listOfOptions;
    // }
  }

}
